.itemCard {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.itemImg {
    width: 100%;
    box-sizing: border-box;
}

.itemPrice {
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
    gap: 9px;
}

.itemName {
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: center;
    min-height: 48px;
}

.itDragging {
    border: 2px solid red;
}

.itemCard a {
    text-decoration: none;
}

.itemCard a > * {
    color: #F2F2F3;
}